html {
  background-color: #e0e0e0;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.weight-canvas {
  width: 100%;
  max-width: 750px;
  margin-bottom: 1em;
}
#weight-buttons {
  max-width: 210px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  gap: 1em;
}
.button-container {
  border-radius: 10px;
background: #e0e0e0;
box-shadow:  15px 15px 30px #868686,
             -15px -15px 30px #ffffff;
}
.button-container p {
  display: inline-block;
  margin: 0;
  margin-right: 1em;
}
.plate-icon {
  max-height: 75px;
}
.remove-button {
  display: inline;
}